@import url(https://fonts.googleapis.com/css2?family=Dosis&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

li {
  list-style: none;
}

.GroceryListForm {
  display: flex;
  justify-content: space-between;
}

.GroceryListForm input {
  border-radius: 5px 0px 0px 5px;
  border: none;
  padding: 0.5rem;
}

.GroceryListForm button {
  border-radius: 0px 5px 5px 0px;
  border: none;
  width: 100%;
  background-color: white;
  text-align: right;
  padding: 10px;
}

.GroceryListItem li {
  margin-top: 0.2rem;
  display: flex;
  justify-content: space-between;
}

.GroceryListItem button {
  border-radius: 20%;
  border: none;
  margin: 5px;
}

.GroceryListItem-rm-btn {
  background-color: #ffce85;
}

.GroceryListItem-qty-btn {
  background-color: white;
}

.GroceryList {
  display: flex;
  flex-direction: column;
}

.GroceryList-total {
  font-size: 0.8rem;
  margin-top: 1rem;
  display: flex;
  justify-content: flex-end;
  border-top: 1px solid #00000067;
}

body {
  background-color: #ffdad7;
}
h1 {
  font-family: "Dosis", sans-serif;
  text-align: center;
  margin-top: 0;
  font-size: 3rem;
  /* color: white; */
  background: rgba(255, 255, 255, 0.342);
  padding: 2rem;
}
.GroceryApp {
  background-color: #ffce85;
  width: 270px;
  margin: 100px auto;
  padding: 20px;
  border-radius: 3px;
  box-shadow: 4px 6px 8px 0px #c5a9a8;
  font-family: "Dosis", sans-serif;
  font-size: 1.2rem;
}
.GroceryApp-title {
  font-size: 1.5rem;
  text-align: center;
  border-bottom: 1px solid #00000067;
  padding-bottom: 0.5rem;
}
.GroceryApp-form {
  margin-bottom: 10px;
}
button {
  outline: none;
  border: none;
}
button:hover {
  cursor: pointer;
}
input {
  outline: none;
  border: none;
}


